<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        {{ name }}
      </v-col>
      <v-col cols="12">
        <h3 class="mb-2">Deskripsi</h3>
        <p>{{ description }}</p>
      </v-col>
    </v-row>
    <v-row class="grey lighten-2">
      <v-col cols="12">
        <h3>Upload Link</h3>
        <div ref="shareLink" class="text-center">{{ shareLink }}</div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-btn color="primary" @click="btnCopyClick">Copy Link</v-btn>
      </v-col>
      <v-col class="12">
        Salin link di atas, kemudian bagikan link tersebut ke semua siswa. 
        Panduan untuk siswa ada di <a 
        href="https://pr.netismedia.com/docs.html#student" 
        target="_blank">halaman web ini</a>.
      </v-col>
    </v-row>
    <v-row no-gutters class="my-5">
      <v-col cols="12" md="6" offset-md="3" v-if="fileList.length>0">
        <v-card class="grey lighten-4">
          <v-card-text>
            <strong class="red--text">Perhatian!</strong> Segera download file 
            yang ditampilkan di sini ke komputer atau hape anda! File tersebut 
            sewaktu-waktu bisa kami hapus apabila kapasitas penyimpanan sudah 
            penuh dan ada pengguna lain yang hendak meng-upload tugas PR 
            mereka. Mohon pengertiannya untuk berbagi <em>bandwidth</em> dan 
            <em>storage space</em>. Terimakasih.
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="10" md="11" class="mb-3">
        <h3>Daftar File</h3>
      </v-col>
      <v-col cols="2" md="1" class="text-right">
        <v-btn icon @click="loadData">
          <v-icon>mdi-restore</v-icon>
        </v-btn>
      </v-col>
      <template v-for="(file, index) of fileList">
        <v-col cols="12" md="9" :key="file[0]" class="d-flex align-center">
          {{ index + 1 }}. {{ file[0] }}
        </v-col>
        <v-col cols="12" md="3" :key="`_${file}`" class="d-flex align-center justify-end mb-1">
          <span>{{ formatBytes(file[1]) }}</span>
          <v-btn icon @click="btnDelClick(index)" :loading="loading">
            <v-icon>mdi-delete-forever</v-icon>
          </v-btn>
          <v-btn icon :href="signedUrl(file[0])" :download="file">
            <v-icon>mdi-download</v-icon>
          </v-btn>
          <v-btn icon :href="signedUrl(file[0])" target="_blank">
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="12" :key="index" v-if="index < (fileList.length-1)">
          <v-divider></v-divider>
        </v-col>
      </template>
      <v-col cols="12" class="grey lighten-3 text-right mt-2 pa-1" v-if="fileList.length>0">
        Total : <strong>{{ formatBytes(totalSize) }}</strong>
      </v-col>
      <v-col cols="12" v-if="fileList.length===0">
        Belum ada yang mengumpulkan tugas.
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="text-center caption">
        &copy; {{ year }} Netismedia
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { deleteObject, getObject, getSignedUrl, listObjectV2 } from '@/lib/aws_lib.js'

export default {
  name: "Task",
  data: () => ({
    name: '',
    description: '',
    fileList: [],
    totalSize: 0,
    loading: false
  }),
  computed: {
    shareLink() {
      let l = window.location
      return `${l.protocol}//${l.host}/murid/#/?a=${this.$route.params.id}`
    },
    year() {
      let d = new Date()
      return d.getFullYear()
    },
  },
  created() {
    this.$store.commit('SET_TOOLBAR', {
      title: 'Tugas Belajar',
      backButton: true
    })
    this.loadData()
  },
  methods: {
    signedUrl(fname) {
      let key = `a/${this.$route.params.id}/${fname}`
      return getSignedUrl(key)
    },
    loadData: async function() {
      let key = `a/${this.$route.params.id}.json`
      this.$store.commit('SET_LOADING', true)
      let resp = await getObject(key, 'json')
      if (200 !== resp.code) {
        console.error(resp.message)
        this.$store.commit('SET_LOADING', false)
        return
      }
      this.name = resp.data.name
      this.description = resp.data.description
      let prefix = `a/${this.$route.params.id}/`
      resp = await listObjectV2(prefix)
      this.$store.commit('SET_LOADING', false)
      if (200 != resp.code) {
        console.error(resp.message)
        return
      }
      this.fileList = []
      this.totalSize = 0
      let self = this
      resp.data.Contents.forEach(el => {
        self.fileList.push([
          el.Key.replace(`a/${this.$route.params.id}/`, ''),
          el.Size
        ])
        self.totalSize += el.Size
      })
    }, 
    btnDelClick: async function(index){
      let goAhead = confirm(`Hapus file ${this.fileList[index][0]}?`)
      if (!goAhead) return
      let key = `a/${this.$route.params.id}/${this.fileList[index][0]}`
      this.loading = true
      let resp = await deleteObject(key)
      this.loading = false
      if (resp.code > 300) {
        console.error(resp.message)
        return
      }
      this.fileList.splice(index, 1)
    },
    btnCopyClick() {
      let range = null
      if (document.selection) {
        range = document.body.createTextRange();
        range.moveToElementText(this.$refs.shareLink);
        range.select().createTextRange();
        document.execCommand("copy");
        alert("Upload link telah disalin ke clipboard.")
      } else if (window.getSelection) {
        range = document.createRange();
        range.selectNode(this.$refs.shareLink);
        let s = window.getSelection()
        s.removeAllRanges()
        s.addRange(range);
        document.execCommand("copy");
        alert("Upload link telah disalin ke clipboard.")
      }
    },
    formatBytes(bytes, decimals=1) {
      if (bytes === 0) return '0 b';

      const k = 1024;
      const dm = decimals < 0 ? 0 : decimals;
      const sizes = ['b', 'kb', 'mb', 'gb', 'tb', 'pb', 'eb', 'zb', 'yb'];

      const i = Math.floor(Math.log(bytes) / Math.log(k));
      const rv = parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];

      return rv.replace('.', ',');
    }
  }
}
</script>
