<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center"
      v-show="!$store.state.authenticated"
    >
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="Nama" name="name"
                prepend-icon="mdi-account" type="text" :readonly="loading"
                v-model="name" :error="loginError" clearable
              />
              <v-text-field label="Token" name="userid"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="userid" :error="loginError" clearable
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text to="/register" color="primary">Daftar</v-btn>
            <v-spacer />
            <v-btn color="primary" @click="btnLoginClick"
              :loading="loading" :disabled="!inputOK"
            >Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-row align="center" justify="center"
      v-show="$store.state.authenticated"
    >
      <v-col cols="12" class="text-center">
        Loading...
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { AUTH_EP } from '@/config.js'
import { httpGet } from '@/lib/fetch_utils.js'
import { saveCredentials } from '@/lib/storage.js'

export default {
  name: "Login",
  data: () => ({
    name: '',
    userid: '',
    loginError: false,
    loading: false,
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.name.length) return false
        if (0 === this.userid.length) return false
        return true
      } catch {
        return false
      }
    }
  },
  methods: {
    btnLoginClick: async function() {
      this.loginError = false
      let url = `${AUTH_EP}/teachers/${this.userid}.json`
      this.loading = true
      let resp = await httpGet(url)
      this.loading = false
      if (resp.code > 300) {
        this.loginError = true
        return
      }
      this.$store.commit('SET_CREDENTIALS', {
        userid: this.userid,
        username: this.name
      })
      let expire = Math.round(Date.now()/1000) + 86400 // 1 day
      saveCredentials(this.name, this.userid, expire)
      this.$store.commit('SET_AUTHENTICATED', true)
      if (Object.prototype.hasOwnProperty.call(this.$route.query, 'redirect')) {
        this.$router.replace((this.$route.query.redirect))
      } else {
        this.$router.replace('/')
      }
    }
  }
}
</script>
