<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="12">
        <v-text-field label="Nama tugas" v-model="name"></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-textarea
          label="Deskripsi"
          solo
          v-model="description"
        ></v-textarea>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="12" class="text-center">
        <v-btn color="primary" :disabled="loading" class="mx-2"
          @click="$router.go(-1)"
        >
          Batal
        </v-btn>
        <v-btn color="primary" :loading="loading" class="mx-2"
          @click="btnSaveClick"
        >
          Save
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { F_ASSGN_FNET_SUFFIX } from '@/config.js'
import cryptoRandomString from '@/lib/random-string.js'
import {
    checkObject, deleteObject, getObject, putObject
  } from '@/lib/aws_lib.js'

export default {
  name: "TasksForm",
  data: (vm) => ({
    F_ASSGN_FNET: `${vm.$store.state.userid}.${F_ASSGN_FNET_SUFFIX}`,
    mode: vm.$route.query.mode,
    id: undefined===vm.$route.query.id?'':vm.$route.query.id,
    name: '',
    description: '',
    metaId: undefined===vm.$route.query.meta?'':vm.$route.query.meta,
    oldData: null,
    loading: false
  }),
  computed: {
    title() {
      if ('add' === this.mode) return 'Tugas Baru'
      return 'Edit Tugas'
    },
  },
  created() {
    if (this.mode === 'edit') {
      this.loadData()
    }
  },
  methods: {
    getB64Name(name) {
      if (name.length > 60) {
        return btoa(name.substring(0, 60))
      }
      return btoa(name)
    },
    loadData: async function() {
      let key = `a/${this.id}.json`
      let resp = await getObject(key, 'json')
      if (200 !== resp.code) {
        console.error(resp.message)
        return
      }
      this.oldData = resp.data
      this.name = resp.data.name
      this.description = resp.data.description
    },
    btnCancelClick(){
      this.$router.go(-1)
    },
    saveJsonData: async function(key, createdTime) {
      let payload = {
        name: this.name,
        description: this.description,
        teacher_name: this.$store.state.username,
        created: createdTime
      }
      let resp = await putObject(key, JSON.stringify(payload), 'application/json')
      return resp
    },
    saveNew: async function() {
      let available = false
      let key = ''
      let taskId = ''
      this.loading = true
      while (!available) {
        taskId = cryptoRandomString({length: 16, type: 'url-safe'})
        key = `a/${taskId}.json`
        let resp = await checkObject(key)
        if (200 !== resp.code) {
          available = true
        }
      }
      
      let createdTs = Math.round(Date.now()/1000)
      let resp = await this.saveJsonData(key, createdTs)
      if (200 !== resp.code) {
        console.error(resp.message)
        this.loading = false
        return
      }
      let b64name = this.getB64Name(this.name)
      let metaKey = `m/${this.$store.state.userid}/${createdTs}.${taskId}.${b64name}.txt`
      resp = await putObject(metaKey, '1', 'text/plain')
      sessionStorage.setItem(this.F_ASSGN_FNET, "1")
      this.loading = false
      this.$router.go(-1)
    },
    saveEdit: async function() {
      let key = `a/${this.id}.json`
      this.loading = true
      let resp = await this.saveJsonData(key, this.oldData.created)
      if (200 !== resp.code) {
        console.error(resp.message)
        this.loading = false
        return
      }
      sessionStorage.setItem(this.F_ASSGN_FNET, "1")
      if(this.name === this.oldData.name) {
        this.loading = false
        this.$router.go(-1)
      } else {
        let oldMetaKey = `m/${this.$store.state.userid}/${this.oldData.created}.${this.id}.${this.metaId}.txt`
        resp = await deleteObject(oldMetaKey)
        let b64name = this.getB64Name(this.name)
        let metaKey = `m/${this.$store.state.userid}/${this.oldData.created}.${this.id}.${b64name}.txt`
        resp = await putObject(metaKey, '1', 'text/plain')
        this.loading = false
        this.$router.go(-1)
      }
    },
    btnSaveClick: async function(){
      if ('' === this.name) return
      if ('' === this.description) return
      if ('add' === this.mode) {
        await this.saveNew()
      } else {
        await this.saveEdit()
      }
    },
  }
}
</script>
