<template>
  <v-container class="fill-height" justify-center fluid>

    <v-row align="center" justify="center"
      v-show="!$store.state.authenticated"
    >
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" v-if="!registerSucceed">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Daftar - GRATIS</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="Nama" name="name"
                prepend-icon="mdi-account" type="text" :readonly="loading"
                v-model="name" :error="registerError" clearable
              />
              <v-text-field label="email" name="email"
                prepend-icon="mdi-email" type="email" :readonly="loading"
                v-model="email" :error="registerError" clearable
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="primary" to="/login" :disabled="loading">
              Login
            </v-btn>
            <v-spacer />
            <v-btn color="primary" @click="btnRegClick"
              :loading="loading" :disabled="!inputOK"
            >Daftar</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="registerSucceed">
          <v-card-text>
            Data pendaftaran anda telah kami catat. Kami akan mengirimkan TOKEN 
            ke alamat email anda paling lambat 1 jam. Jika sudah mendapatkan 
            TOKEN, silahkan anda <router-link to="/login">LOGIN</router-link> 
            untuk menggunakan fasilitas ini. Terimakasih.
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row align="center" justify="center"
      v-show="$store.state.authenticated"
    >
      <v-col cols="12" class="text-center">
        Loading...
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
"use strict"

import PahoMQTT from 'paho-mqtt'

import { MQTT_BROKER, MQTT_PATH, MQTT_PORT, MQTT_TOPIC, REGSHEET_URL } from '@/config.js'
import cryptoRandomString from '@/lib/random-string.js'
import { postForm } from '@/lib/fetch_utils.js'

export default {
  name: "Register",
  data: () => ({
    name: '',
    email: '',
    registerSucceed: false,
    registerError: false,
    loading: false,
    mqttClient: null,
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.name.length) return false
        if (0 === this.email.length) return false
        return true
      } catch {
        return false
      }
    }
  },
  methods: {
    sendMessage() {
      let self = this
      // eslint-disable-next-line
      return new Promise((resolve, reject) => {
        let clientId = cryptoRandomString({length: 8, type: 'url-safe'})
        self.mqttClient = new PahoMQTT.Client(
          MQTT_BROKER, MQTT_PORT, MQTT_PATH, clientId
        )
        // eslint-disable-next-line
        self.mqttClient.onConnected = function(reconnect, URI) {
          let payload = JSON.stringify({
            event: 'register',
            name: self.name,
            email: self.email
          })
          self.mqttClient.send(MQTT_TOPIC, payload)
          self.mqttClient.disconnect()
          self.mqttClient = null
          resolve()
        }
        let connectionOpts = {
          cleanSession: true,
          useSSL: true,
          keepAliveInterval: 10,
          reconnect: true
        }
        self.mqttClient.connect(connectionOpts)
      })
    },
    btnRegClick: async function() {
      let params = {
        name: this.name,
        email: this.email
      }
      this.loading = true
      let resp = await postForm(REGSHEET_URL, params)
      this.loading = false
      if (200 === resp.code) {
        this.registerSucceed = true
        try {
          await this.sendMessage()
        } catch (error) {
          console.error(error)
        }
      } else {
        alert("Tidak bisa memproses pendaftaran anda! " + resp.message)
      }
    },
  }
}
</script>
