<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list v-if="assignments.length>0" class="ly-list">
      <template v-for="(assignment, index) of assignments">
        <v-list-item :key="assignment[1]">
          <v-row no-gutters>
            <v-col cols="12" md="9" class="pr-assignment" @click.stop="displayAssignment(index)">
              {{ assignment[3] }}
            </v-col>
            <v-col cols="6" md="3" offset="6" offset-md="0" class="text-right">
              <span>{{ formatIdDate(assignment[0]) }}</span>
              <v-btn icon @click.stop="onDelClick(index)">
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon @click.stop="onEditClick(index)">
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==(assignments.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row v-if="(assignments.length==0) && pageReady" class="mx-0 centered-row">
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>
            Belum ada data!
          </v-card-title>
          <v-card-text>
            Tambahkan data tugas belajar dengan meng-klik tombol yang ada di sudut 
            kanan-bawah layar!
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn fab fixed bottom right dark color="pink" @click.stop="onFabAddClick">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
"use strict"

import { F_ASSGN_FNET_SUFFIX } from '@/config.js'
import { deleteObject, listObjectV2, putObject } from '@/lib/aws_lib.js'

export default {
  name: "Tasks",
  data: (vm) => ({
    assignments: [],
    pageReady: false,
    TASSIGN_KEY: `${vm.$store.state.userid}.pr.assignments`,
    F_ASSGN_FNET: `${vm.$store.state.userid}.${F_ASSGN_FNET_SUFFIX}`
  }),
  created() {
    this.$store.commit('SET_TOOLBAR', { title: 'Tugas Belajar' })
    this.loadData()
  },
  methods: {
    onFabAddClick(){
      this.$router.push({
        name: 'taskform',
        query: {
          mode: 'add'
        }
      })
    },
    formatIdDate: function(ts) {
      let d = new Date(1000 * ts)
      return `${d.getDate()}/${1 + d.getMonth()}/${d.getFullYear()}`
    },
    loadData: async function() {
      let x = sessionStorage.getItem(this.F_ASSGN_FNET)
      if (null !== x) {
        sessionStorage.removeItem(this.F_ASSGN_FNET)
        this.loadDataFromNet()
        return
      }
      let d = sessionStorage.getItem(this.TASSIGN_KEY)
      if (d === null) {
        this.loadDataFromNet()
        return
      }
      let tn = Date.now()
      let p = JSON.parse(d)
      if ((240000 + p.ts) < tn) {
        sessionStorage.removeItem(this.TASSIGN_KEY)
        this.loadDataFromNet()
      } else {
        this.assignments = p.data
        this.pageReady = true
      }
    },
    loadDataFromNet: async function() {
      let prefix = `m/${this.$store.state.userid}/`
      this.$store.commit('SET_LOADING', true)
      let resp = await listObjectV2(prefix)
      if (200 !== resp.code) {
        console.error(resp.message)
        this.pageReady = true
        this.$store.commit('SET_LOADING', false)
        return
      }
      resp.data.Contents.reverse()
      this.assignments = []
      let self = this
      resp.data.Contents.forEach(c => {
        let s = c.Key.replace(prefix, '').split('.')
        self.assignments.push(
          [
            parseInt(s[0]), s[1], s[2], atob(s[2])
          ]
        )
      })
      let payload = {
        ts: Date.now(),
        data: this.assignments
      }
      sessionStorage.setItem(this.TASSIGN_KEY, JSON.stringify(payload))
      this.pageReady = true
      this.$store.commit('SET_LOADING', false)
    },
    displayAssignment(index){
      this.$router.push({
        name: 'task',
        params: {
          id: this.assignments[index][1]
        }
      })
    },
    onEditClick(index){
      this.$router.push({
        name: 'taskform',
        query: {
          mode: 'edit',
          id: this.assignments[index][1],
          meta: this.assignments[index][2]
        }
      })
    },
    onDelClick: async function(index) {
      let goAhead = window.confirm('Yakin menghapus ' + this.assignments[index][3] + '?')
      if (!goAhead) return
      let s = this.assignments[index]
      let dataKey = `a/${s[1]}.json`
      let metaKey = `m/${this.$store.state.userid}/${s[0]}.${s[1]}.${s[2]}.txt`
      let delKey = `d/${s[1]}.txt`
      this.$store.commit('SET_LOADING', true)
      let resp = await deleteObject(dataKey)
      if (resp.code >= 300) {
        console.error(resp.message)
        this.$store.commit('SET_LOADING', false)
        return
      }
      resp = await deleteObject(metaKey)
      this.$store.commit('SET_LOADING', false)
      if (resp.code >= 300) {
        console.error(resp.message)
        return
      }
      sessionStorage.setItem(this.F_ASSGN_FNET, "1")
      this.assignments.splice(index, 1)
      resp = await putObject(delKey, '1', 'text/plain')
    },
  }
}
</script>

<style scoped>
.ly-list {
  margin-bottom: 64px;
}

.pr-assignment {
  cursor: pointer
}
</style>
