'use strict'

const PREFIX = 'hwtch' // homework management for teacher

export function loadCredentials() {
    let credentials = {}
    let key = `${PREFIX}:name`
    let name = localStorage.getItem(key)
    if (null === name) return undefined

    key = `${PREFIX}:userid`
    let userid = localStorage.getItem(key)
    if (null === userid) return undefined
    key = `${PREFIX}:exp`
    let expStr = localStorage.getItem(key)
    if (null === expStr) {
        localStorage.removeItem(`${PREFIX}:userid`)
        return undefined
    }
    let exp = parseInt(expStr)
    let nowTs = Math.round(Date.now()/1000)
    if (exp < nowTs) {
        localStorage.removeItem(`${PREFIX}:userid`)
        localStorage.removeItem(`${PREFIX}:exp`)
        return undefined
    }
    credentials.name = name
    credentials.userid = userid
    credentials.exp = exp
    return credentials
}

export function saveCredentials(name, userid, exp) {
    let key = `${PREFIX}:name`
    localStorage.setItem(key, name)
    key = `${PREFIX}:userid`
    localStorage.setItem(key, userid)
    key = `${PREFIX}:exp`
    localStorage.setItem(key, `${exp}`)
}

export function clearCredentials() {
    let key = `${PREFIX}:name`
    localStorage.removeItem(key)
    key = `${PREFIX}:userid`
    localStorage.removeItem(key)
    key = `${PREFIX}:exp`
    localStorage.removeItem(key)
}