'use strict'

import S3 from 'aws-sdk/clients/s3'

import {
        S3_REGION, S3_ENDPOINT_URL, S3_ACCESS_KEY_ID, S3_SECRET_ACCESS_KEY,
        S3_BUCKET
    } from '@/config.js'
import * as sigv4 from '@/lib/sigv4'

const INTERVAL = 3600000 // 60 minutes

export function getS3Client() {
    return new S3({
        endpoint: S3_ENDPOINT_URL,
        accessKeyId: S3_ACCESS_KEY_ID,
        secretAccessKey: S3_SECRET_ACCESS_KEY,
        region: S3_REGION,
        s3ForcePathStyle: true,
        signatureVersion: 'v4'
    })
}

export async function getObject(key, dataType='string') {
    let s3 = getS3Client()
    try {
        let resp = await s3.getObject({
            Bucket: S3_BUCKET, Key: key
        }).promise()
        
        let data = null
        if ('string'===dataType) {
            data = resp.Body.toString()
        } else if ('json'===dataType) {
            data = JSON.parse(resp.Body.toString())
        } else {
            data = resp.Body.buffer
        }
        return { code: 200, data: data }
    } catch (error) {
        return { code: error.statusCode, message: error.code }
    }
}

export async function checkObject(key) {
    let s3 = getS3Client()
    try {
        // eslint-disable-next-line
        let resp = await s3.headObject({
            Bucket: S3_BUCKET, Key: key
        }).promise()
        return { code: 200 }
    } catch (error) {
        return { code: error.statusCode, message: error.code }
    }
}

export async function putObject(key, payload, contentType) {
    let s3 = getS3Client()
    let params = {
        Bucket: S3_BUCKET,
        Key: key,
        Body: payload,
        ContentType: contentType
    }
    try {
        let resp = await s3.putObject(params).promise()
        return { code: resp["$response"].httpResponse.statusCode }
    } catch (error) {
        return { code: error.statusCode, message: error.code }
    }
}

export async function deleteObject(key){
    let s3 = getS3Client()
    let params = {
        Bucket: S3_BUCKET,
        Key: key
    }
    try {
        let resp = await s3.deleteObject(params).promise()
        return { code: resp["$response"].httpResponse.statusCode }
    } catch (error) {
        return { code: error.statusCode, message: error.code }
    }
}

export async function listObjectV2(prefix){
    let s3 = getS3Client()
    let params = {
        Bucket: S3_BUCKET,
        Prefix: prefix
    }
    try {
        let resp = await s3.listObjectsV2(params).promise()
        return {
            code: resp["$response"].httpResponse.statusCode,
            data: resp
        }
    } catch (error) {
        return { code: error.statusCode, message: error.code }
    }
}

export function getSignedUrl(key, expires=7200) {
    let params = { Bucket: S3_BUCKET, Key: key, Expires: expires }
    let s3 = getS3Client()
    return s3.getSignedUrl('getObject', params)
}

export function s3PreSignWCache(key, expires) {
  let nowTs = Date.now()
  let baseTime = nowTs - (nowTs % INTERVAL) // tail
  let _h = S3_ENDPOINT_URL.split('://')
  let protocol = _h[0]
  let host = S3_BUCKET + '.' + _h[1]
  let path = '/' + key
  let url = sigv4.createPresignedURL('GET', host, path, 's3', 
      "UNSIGNED-PAYLOAD", {
    key: S3_ACCESS_KEY_ID,
    secret: S3_SECRET_ACCESS_KEY,
    protocol: protocol,
    timestamp: baseTime,
    region: S3_REGION,
    expires: expires,
    signSessionToken: false
  })
  return url
}
